@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,600;0,800;0,900;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  padding: 0px;
  margin: 0px;
  background: #f4f1f1;
  font-family: "Nunito Sans", sans-serif !important;
}
div#container {
  width: 1024px;
  font-family: "Nunito Sans", sans-serif !important;
  margin: 30px auto;
}

.quoteoftheday {
  font-weight: bold;
  font-size: 25px;
  display: inline-block;
  vertical-align: baseline;
  width: 300px;
}

.name {
  width: 720px;
  display: inline-block;
  vertical-align: baseline;
  text-align: right;
  font-size: 33px;
  font-weight: bold;
}

article {
  padding: 100px 0px;
}

.quote h1 {
  font-family: "Nunito Sans", sans-serif !important;
  font-size: 100px;
  line-height: 100px;
  margin: 0px;
  font-weight: bold;
}

.quoteby h4 {
  font-size: 31px;
  margin: 0px;
  margin-top: 19px;
  font-weight: bold;
}

.skills {
  width: 250px;
  display: inline-block;
  vertical-align: top;
}

.content {
  width: 550px;
  display: inline-block;
  vertical-align: top;
}
.content p {
  font-size: 19px;
  padding-right: 27px;
  font-weight: 700;
  margin: 0px;
}

.skills h6 {
  font-size: 12px;
  margin: 0px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #666;
  border-bottom: 1px solid #666;
  padding-bottom: 10px;
  margin-bottom: 10px;
  font-weight: bold;
  display: inline-block;
}

.skills ul {
  margin: 0px;
  padding: 0px;
}

.skills ul li {
  list-style: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 26px;
}
.skills ul li a,
.content,
.content a {
  color: black;
}

a {
  text-decoration: none;
}
@media only screen and (max-width: 1024px) {
  div#container {
    width: 310px;
  }
  .quoteoftheday {
    width: 100%;
  }
  .name {
    width: 100%;
    text-align: left;
  }
  .quote h1 {
    font-size: 40px;
    line-height: 40px;
  }
  .quoteby h4 {
    font-size: 25px;
    margin-top: 15px;
  }
  article {
    padding: 50px 0px;
  }
  .skills {
    width: 100%;
    margin-bottom: 20px;
  }
  .content {
    width: 100%;
  }
  .content p {
    font-size: 17px;
    padding-right: 0px;
    margin: 0px;
  }
}

.social {
  -webkit-text-decoration: wavy;
          text-decoration: wavy;
  font-size: 25px;
}

.social:hover {
  background-color: #666;
  opacity: 0.7;
  cursor: pointer;
}

